const uni = require('uni').default;











import pageHead from '../../../components/page-head.vue'

export default {
	data() {
		return {
			title: 'modal',
			modalHidden: true,
			modalHidden2: true
		}
	},
	methods: {
		modalTap: function (e) {
			uni.showModal({
				title: "弹窗标题",
				content: "弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内",
				showCancel: false,
				confirmText: "确定"
			})
		},
		noTitlemodalTap: function (e) {
			uni.showModal({
				content: "弹窗内容，告知当前状态、信息和解决方法，描述文字尽量控制在三行内",
				confirmText: "确定",
				cancelText: "取消"
			})
		}
	},
	components: {
		pageHead

	}
}



